import mongoose from 'mongoose'
const Schema = mongoose.Schema

const historySchema = new Schema(
  {
    title: { type: String },
    date: { type: String },
    tags: { type: Array },
    editor: { type: String },
  },
  {
    toObject: { virtuals: true },
    toJSON: { virtuals: true },
    versionKey: false,
    timestamps: true,
  }
)

export const History = mongoose.model('history', historySchema)
